.logo {
  height: 38px;
  width: 130px;
  background-image: url(/images/MyBiKE_logo_white.svg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include onTablet {
    height: 56px;
    width: 187px;
  }

  &--black {
    background-image: url(/images/MyBiKE_logo_black.svg);
  }
}
